.feedback {
    width: 100%;
    max-width: 100%;
    background-color: rgba(250, 250, 250, 1);
}

.feedback__container {
    max-width: 1350px;
    margin: 0 auto;
}

.feedback__wrap {
    display: flex;
    flex-direction: column;
    padding: 136px 101px 144px;
}

.feedback__feed {
    display: flex;
    justify-content: space-between;
}

.feedback__content {
    width: 50%;
    display: flex;
    flex-direction: column;
    padding-left: 10.2%;
    padding-top: 66px;
}

.feedback__name {
    font-size: 36px;
    line-height: 44px;
    color: #080808;
    margin-bottom: 22px;
}

.feedback__about {
    font-size: 16px;
    line-height: 22px;
    color: #080808;
    width: 35%;
    margin-bottom: 49px;
}

.feedback__text {
    font-weight: 300;
    font-size: 20px;
    line-height: 34.13px;
    color: #080808;
    opacity: 0.7;
    width: 83%;
    position: relative;
    letter-spacing: -0.05px;
}

.feedback__text::before {
    content: "";
    position: absolute;
    background: url("../img/quotes-desk.svg") no-repeat center;
    width: 16px;
    height: 13px;
    left: -49px;
    top: 10px;
}

.feedback__picture {
    width: max-content;
}

.feedback__feed-info {
    display: flex;
    justify-content: space-between;
    padding-left: 4.5%;
}

.feedback__picture-info {
    width: max-content;
    transform: translate(0, -19.8%);
}

.feedback__content-info {
    padding-top: 96px;
    width: 47.1%;
}

.feedback__content-info-list {
    display: flex;
    justify-content: space-between;
    padding-right: 6.2%;
    margin-bottom: 53px;
}

.feedback__content-info-item {
    display: flex;
    flex-direction: column;
}

.feedback__content-info-item:nth-child(2) {
    padding-right: 23px;
}

.feedback__element {
    font-size: 56px;
    line-height: 68px;
    color: #080808;
    margin-bottom: 6px;
}

.feedback__par {
    font-weight: 300;
    font-size: 14px;
    line-height: 18.48px;
    color: #080808;
    width: 70%;
}

.feedback__text-par {
    font-weight: 300;
    font-size: 14px;
    line-height: 23.89px;
    color: #080808;
}

@media (max-width: 1140px) {
    .feedback__picture-info {
        width: max-content;
        transform: translate(0, 0);
    }

    .feedback__content {
        padding-top: 0;
    }
}

@media (max-width: 1023px) {
    .feedback__feed {
        margin-bottom: 20px;
    }

    .feedback__content {
        flex-basis: 50%;
    }

    .feedback__picture {
        flex-basis: 50%;
    }

    .feedback__feed-info {
        flex-wrap: wrap;
    }

    .feedback__picture-info {
        width: 100%;
        display: flex;
        justify-content: center;
    }

    .feedback__content-info {
        width: 100%;
    }
}

@media (max-width: 768px) {

    .feedback__wrap {
        padding: 136px 50px 144px;
    }

    .feedback__feed {
        flex-wrap: wrap;
    }

    .feedback__content {
        flex-basis: 100%;
        margin-bottom: 20px;
    }

    .feedback__picture {
        flex-basis: 100%;
    }
}

@media (max-width: 530px) {
    .feedback__wrap {
        padding: 136px 20px 144px;
    }
}